'use client';

import { useState, useCallback } from 'react';
import { Upload, FileText, AlertCircle, CheckCircle, X } from 'lucide-react';
import { uploadFile } from '../lib/api-client';
import { UploadResult } from '../types/enterprise';

export default function UploadPage() {
  const [isDragging, setIsDragging] = useState(false);
  const [uploadResult, setUploadResult] = useState<UploadResult | null>(null);
  const [isUploading, setIsUploading] = useState(false);

  // Переменные для работы с API2
  const API2 = (process.env.NEXT_PUBLIC_API2_URL || 'http://127.0.0.1:8000/api').replace(/\/$/, '');
  const [api2File, setApi2File] = useState<File | null>(null);
  const [api2Msg, setApi2Msg] = useState<string>('');
  const [api2Data, setApi2Data] = useState<any[]>([]);
  const [api2Loading, setApi2Loading] = useState(false);

  const handleDragOver = useCallback((e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(true);
  }, []);

  const handleDragLeave = useCallback((e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(false);
  }, []);

  const handleDrop = useCallback((e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(false);
    const files = Array.from(e.dataTransfer.files);
    if (files.length > 0) {
      handleFileUpload(files[0]);
    }
  }, []);

  const handleFileSelect = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (files && files.length > 0) {
      handleFileUpload(files[0]);
    }
  };

  // Загрузка напрямую в API2
  const uploadToApi2 = async () => {
    if (!api2File) {
      setApi2Msg('Выберите файл');
      return;
    }
    setApi2Loading(true);
    setApi2Msg('Загрузка...');
    try {
      const fd = new FormData();
      fd.append('file', api2File);
      // без credentials — чтобы не упереться в CORS
      const res = await fetch(`${API2}/upload`, { method: 'POST', body: fd });
      if (!res.ok) {
        const t = await res.text();
        setApi2Msg(`Ошибка: ${res.status} ${t}`);
        return;
      }
      const json = await res.json().catch(() => ({}));
      setApi2Msg(`✔ API2: импортировано ${json.imported ?? json.count ?? 'ok'}`);

      // Теперь получаем загруженные данные для отображения
      try {
        const dataRes = await fetch(`${API2}/state/records`);
        if (dataRes.ok) {
          const dataJson = await dataRes.json();
          setApi2Data(dataJson.records || []);
        }
      } catch (err) {
        console.warn('Не удалось получить данные для отображения:', err);
      }
    } catch (err: any) {
      setApi2Msg(`Ошибка: ${err.message || String(err)}`);
    } finally {
      setApi2Loading(false);
    }
  };

  const handleFileUpload = async (file: File) => {
    setIsUploading(true);
    setUploadResult(null);

    try {
      // Отправляем файл на бэкенд
      const result = await uploadFile(file);
      setUploadResult(result);
    } catch (error) {
      setUploadResult({
        success: false,
        message: `Ошибка при обработке файла: ${(error as Error).message}`,
        processedCount: 0,
        errorCount: 1,
        errors: [(error as Error).message]
      });
    } finally {
      setIsUploading(false);
    }
  };

  const clearResults = () => {
    setUploadResult(null);
  };

  return (
    <div className="max-w-4xl mx-auto space-y-8">
      {/* Заголовок */}
      <div>
        <h1 className="text-3xl font-bold text-gray-900">Загрузка данных предприятий</h1>
        <p className="text-gray-600 mt-2">
          Загрузите файл CSV или Excel с данными о предприятиях Москвы для анализа
        </p>
      </div>

      {/* Зона загрузки */}
      <div className="bg-white rounded-lg shadow-sm border">
        <div
          className={`
            border-2 border-dashed rounded-lg p-12 text-center transition-colors
            ${isDragging 
              ? 'border-blue-400 bg-blue-50' 
              : 'border-gray-300 hover:border-gray-400'
            }
          `}
          onDragOver={handleDragOver}
          onDragLeave={handleDragLeave}
          onDrop={handleDrop}
        >
          <div className="flex flex-col items-center space-y-4">
            <div className="p-4 bg-gray-100 rounded-full">
              <Upload className="w-8 h-8 text-gray-600" />
            </div>

            <div>
              <h3 className="text-lg font-semibold text-gray-900">
                Перетащите файл сюда или в��берите файл
              </h3>
              <p className="text-gray-500 mt-1">
                Поддерживаются форматы: CSV, Excel (XLSX, XLS)
              </p>
            </div>

            <div className="flex flex-col sm:flex-row gap-4">
              <label className="bg-blue-600 text-white px-6 py-3 rounded-lg hover:bg-blue-700 cursor-pointer transition-colors">
                <input
                  type="file"
                  accept=".csv,.xlsx,.xls"
                  onChange={handleFileSelect}
                  className="hidden"
                />
                Выбрать файл
              </label>

              <button
                onClick={() => {
                  // Загружаем демонстрационные данные
                  const demoResult: UploadResult = {
                    success: true,
                    message: 'Загружены демонстрационные данные',
                    processedCount: 100,
                    errorCount: 5,
                    errors: [
                      'Строка 15: Некорректная отрасль',
                      'Строка 23: Отсутствует адрес',
                      'Строка 34: Некорректное количество сотрудников',
                      'Строка 67: Некорректный регион',
                      'Строка 89: Некорректная выручка'
                    ]
                  };
                  setUploadResult(demoResult);
                }}
                className="border border-gray-300 text-gray-700 px-6 py-3 rounded-lg hover:bg-gray-50 transition-colors"
              >
                Загрузить демо-данные
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* Индикатор загрузки */}
      {isUploading && (
        <div className="bg-blue-50 border border-blue-200 rounded-lg p-6">
          <div className="flex items-center space-x-3">
            <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-blue-600"></div>
            <div>
              <p className="font-medium text-blue-900">Обработка файла...</p>
              <p className="text-blue-700 text-sm">Пожалуйста, подождите</p>
            </div>
          </div>
        </div>
      )}

      {/* Результат загрузки */}
      {uploadResult && (
        <div className={`
          rounded-lg p-6 border
          ${uploadResult.success 
            ? 'bg-green-50 border-green-200' 
            : 'bg-red-50 border-red-200'
          }
        `}>
          <div className="flex items-start justify-between">
            <div className="flex items-start space-x-3">
              {uploadResult.success ? (
                <CheckCircle className="w-6 h-6 text-green-600 flex-shrink-0" />
              ) : (
                <AlertCircle className="w-6 h-6 text-red-600 flex-shrink-0" />
              )}
              <div>
                <h3 className={`font-semibold ${
                  uploadResult.success ? 'text-green-900' : 'text-red-900'
                }`}>
                  {uploadResult.success ? 'Загрузка завершена' : 'Ошибка загрузки'}
                </h3>
                <p className={`mt-1 ${
                  uploadResult.success ? 'text-green-700' : 'text-red-700'
                }`}>
                  {uploadResult.message}
                </p>
              </div>
            </div>
            <button
              onClick={clearResults}
              className="text-gray-400 hover:text-gray-600"
            >
              <X className="w-5 h-5" />
            </button>
          </div>

          {/* Статистика */}
          <div className="mt-4 grid grid-cols-1 sm:grid-cols-2 gap-4">
            <div className="bg-white p-4 rounded-lg border">
              <div className="text-center">
                <p className="text-2xl font-bold text-green-600">{uploadResult.processedCount}</p>
                <p className="text-sm text-gray-600">Обработано записей</p>
              </div>
            </div>
            <div className="bg-white p-4 rounded-lg border">
              <div className="text-center">
                <p className="text-2xl font-bold text-red-600">{uploadResult.errorCount}</p>
                <p className="text-sm text-gray-600">Ошибок</p>
              </div>
            </div>
          </div>

          {/* Ошибки */}
          {uploadResult.errors.length > 0 && (
            <div className="mt-4">
              <h4 className="font-medium text-gray-900 mb-2">Найденные ошибки:</h4>
              <div className="bg-white rounded-lg border max-h-40 overflow-y-auto">
                <div className="p-4 space-y-2">
                  {uploadResult.errors.slice(0, 10).map((error, index) => (
                    <div key={index} className="flex items-start space-x-2">
                      <div className="w-2 h-2 bg-red-400 rounded-full mt-2 flex-shrink-0"></div>
                      <p className="text-sm text-gray-700">{error}</p>
                    </div>
                  ))}
                  {uploadResult.errors.length > 10 && (
                    <p className="text-sm text-gray-500 italic">
                      И ещё {uploadResult.errors.length - 10} ошибок...
                    </p>
                  )}
                </div>
              </div>
            </div>
          )}
        </div>
      )}

      {/* Второй API (новый формат) */}
      <div className="bg-white rounded-lg shadow-sm border p-6">
        <h2 className="text-xl font-semibold text-gray-900 mb-3">
          Второй API (новый формат)
        </h2>
        <div className="flex flex-col sm:flex-row gap-4 items-center">
          <input
            type="file"
            accept=".csv,.xlsx,.xls"
            onChange={(e) => setApi2File(e.target.files?.[0] || null)}
            className="block w-full text-sm text-gray-500 file:mr-4 file:py-2 file:px-4 file:rounded-full file:border-0 file:text-sm file:font-semibold file:bg-blue-50 file:text-blue-700 hover:file:bg-blue-100"
          />
          <button
            className="bg-blue-600 text-white px-6 py-3 rounded-lg hover:bg-blue-700 transition-colors whitespace-nowrap"
            onClick={uploadToApi2}
            disabled={!api2File}
          >
            Загрузить в API2
          </button>
        </div>
        <p className="text-xs text-gray-500 mt-2">Отправка на: {API2}/upload</p>
        {api2Msg && (
          <div className={`mt-3 p-3 rounded-lg text-sm ${
            api2Msg.includes('✔') 
              ? 'bg-green-50 text-green-800 border border-green-200' 
              : api2Msg.includes('Ошибка') 
                ? 'bg-red-50 text-red-800 border border-red-200'
                : 'bg-blue-50 text-blue-800 border border-blue-200'
          }`}>
            {api2Msg}
          </div>
        )}
      </div>

      {/* Индикатор загрузки API2 */}
      {api2Loading && (
        <div className="bg-blue-50 border border-blue-200 rounded-lg p-6">
          <div className="flex items-center space-x-3">
            <div className="animate-spin rounded-full h-6 w-6 border-b-2 border-blue-600"></div>
            <div>
              <p className="font-medium text-blue-900">Загрузка в API2...</p>
              <p className="text-blue-700 text-sm">Обработка файла и получение данных</p>
            </div>
          </div>
        </div>
      )}

      {/* Отображение данных из API2 */}
      {api2Data.length > 0 && (
        <div className="bg-white rounded-lg shadow-sm border p-6">
          <div className="flex items-center justify-between mb-4">
            <h3 className="text-lg font-semibold text-gray-900">
              Данные из API2 ({api2Data.length} записей)
            </h3>
            <button
              onClick={() => setApi2Data([])}
              className="text-gray-400 hover:text-gray-600"
            >
              <X className="w-5 h-5" />
            </button>
          </div>

          {/* Статистика API2 */}
          <div className="grid grid-cols-1 sm:grid-cols-3 gap-4 mb-6">
            <div className="bg-blue-50 p-4 rounded-lg border border-blue-200">
              <div className="text-center">
                <p className="text-2xl font-bold text-blue-600">{api2Data.length}</p>
                <p className="text-sm text-blue-800">Всего записей</p>
              </div>
            </div>
            <div className="bg-green-50 p-4 rounded-lg border border-green-200">
              <div className="text-center">
                <p className="text-2xl font-bold text-green-600">
                  {api2Data.filter(item => item.inn).length}
                </p>
                <p className="text-sm text-green-800">С ИНН</p>
              </div>
            </div>
            <div className="bg-purple-50 p-4 rounded-lg border border-purple-200">
              <div className="text-center">
                <p className="text-2xl font-bold text-purple-600">
                  {api2Data.filter(item => item.naimenovanie_organizacii).length}
                </p>
                <p className="text-sm text-purple-800">С названием</p>
              </div>
            </div>
          </div>

          {/* Таблица с данными */}
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    ИНН
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Название организации
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Основная отрасль
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Статус
                  </th>
                  <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                    Адрес
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {api2Data.slice(0, 10).map((item, index) => (
                  <tr key={index} className="hover:bg-gray-50">
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {item.inn || '-'}
                    </td>
                    <td className="px-6 py-4 text-sm text-gray-900 max-w-xs truncate">
                      {item.naimenovanie_organizacii || item.polnoe_naimenovanie_organizacii || '-'}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      {item.osnovnaya_otrasl || '-'}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900">
                      <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${
                        item.status_itog === 'Активно' || item.status_spark === 'Действующая' 
                          ? 'bg-green-100 text-green-800'
                          : 'bg-gray-100 text-gray-800'
                      }`}>
                        {item.status_itog || item.status_spark || '-'}
                      </span>
                    </td>
                    <td className="px-6 py-4 text-sm text-gray-900 max-w-xs truncate">
                      {item.yuridicheskii_adres || item.adres_proizvodstva || '-'}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>

          {api2Data.length > 10 && (
            <div className="mt-4 text-center">
              <p className="text-sm text-gray-600">
                Показано 10 из {api2Data.length} записей.
                <span className="text-blue-600 ml-1">Полные данные доступны через API</span>
              </p>
            </div>
          )}

          {/* Кнопки для работы с данными API2 */}
          <div className="mt-6 flex flex-wrap gap-3">
            <button
              onClick={async () => {
                try {
                  const response = await fetch(`${API2}/state/records`);
                  if (response.ok) {
                    const data = await response.json();
                    setApi2Data(data.records || []);
                    setApi2Msg(`✔ Обновлено: ${data.records?.length || 0} записей`);
                  }
                } catch (err) {
                  setApi2Msg('Ошибка обновления данных');
                }
              }}
              className="bg-green-600 text-white px-4 py-2 rounded-lg hover:bg-green-700 transition-colors text-sm"
            >
              Обновить данные
            </button>
            <button
              onClick={async () => {
                try {
                  const response = await fetch(`${API2}/state/clear`, { method: 'POST' });
                  if (response.ok) {
                    setApi2Data([]);
                    setApi2Msg('✔ Данные очищены');
                  }
                } catch (err) {
                  setApi2Msg('Ошибка очистки данных');
                }
              }}
              className="bg-red-600 text-white px-4 py-2 rounded-lg hover:bg-red-700 transition-colors text-sm"
            >
              Очистить данные
            </button>
            <button
              onClick={() => {
                const jsonString = JSON.stringify(api2Data, null, 2);
                const blob = new Blob([jsonString], { type: 'application/json' });
                const url = URL.createObjectURL(blob);
                const a = document.createElement('a');
                a.href = url;
                a.download = `api2_data_${new Date().toISOString().split('T')[0]}.json`;
                a.click();
                URL.revokeObjectURL(url);
              }}
              className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 transition-colors text-sm"
            >
              Скачать JSON
            </button>
          </div>
        </div>
      )}

      {/* Инструкции */}
      <div className="bg-gray-50 rounded-lg p-6">
        <h3 className="text-lg font-semibold text-gray-900 mb-4">Формат данных</h3>
        <div className="space-y-4">
          <p className="text-gray-700">
            Файл должен содержать следующие столбцы:
          </p>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="space-y-2">
              <div className="flex items-center space-x-2">
                <FileText className="w-4 h-4 text-blue-600" />
                <span className="font-medium">name</span> - Название предприятия
              </div>
              <div className="flex items-center space-x-2">
                <FileText className="w-4 h-4 text-blue-600" />
                <span className="font-medium">industry</span> - Отрасль
              </div>
              <div className="flex items-center space-x-2">
                <FileText className="w-4 h-4 text-blue-600" />
                <span className="font-medium">region</span> - Регион Москвы
              </div>
              <div className="flex items-center space-x-2">
                <FileText className="w-4 h-4 text-blue-600" />
                <span className="font-medium">employees</span> - Количество сотрудников
              </div>
            </div>
            <div className="space-y-2">
              <div className="flex items-center space-x-2">
                <FileText className="w-4 h-4 text-blue-600" />
                <span className="font-medium">revenue</span> - Выручка (руб.)
              </div>
              <div className="flex items-center space-x-2">
                <FileText className="w-4 h-4 text-blue-600" />
                <span className="font-medium">address</span> - Адрес
              </div>
              <div className="flex items-center space-x-2">
                <FileText className="w-4 h-4 text-blue-600" />
                <span className="font-medium">phone</span> - Телефон (опционально)
              </div>
              <div className="flex items-center space-x-2">
                <FileText className="w-4 h-4 text-blue-600" />
                <span className="font-medium">email</span> - Email (опционально)
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
